.menu {
  height: 100vh;

  background-color: $menu-background-color;

  font-size: 22px;
  font-weight: 600;
  text-transform: uppercase;
  color: $main-color;

  overflow: auto;

  &__top {
    position: sticky;

    top: 0;
    z-index: 1;

    @include content-padding-inline;

    padding-inline: 20px;
    margin-bottom: 8px;

    @include on-tablet {
      padding-inline: 72px;
    }

    @include on-desktop {
      padding-inline: 120px;
    }
  }

  &__content {
    @include content-padding-inline;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
